  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    /* stylelint-disable-next-line */
